import React from 'react';

const scoutingEvents = [
  {
    title: 'Trans Nzoia Maize Scouting Field Day',
    date: '2026-02-10',
    location: 'Kitale, Trans Nzoia',
    host: 'CIMMYT & County Agriculture Office',
    description: 'Hands-on demonstration of maize pest scouting and identification. Open to all farmers.'
  },
  {
    title: 'Grasshopper & Stalk Borer Scouting Workshop',
    date: '2026-02-18',
    location: 'Endebess Farmers Training Centre',
    host: 'KALRO',
    description: 'Learn how to scout for grasshoppers and stalk borers. Includes field walk and Q&A.'
  },
  {
    title: 'Integrated Pest Management (IPM) Scouting Seminar',
    date: '2026-03-05',
    location: 'Cherangany Maize Cooperative',
    host: 'FAO & Local Extension',
    description: 'IPM best practices, pest scouting, and reporting. Free for registered members.'
  }
];

const scoutingRules = [
  'Scout fields 2-3 times per week, especially after rain.',
  'Check at least 5 locations per field (4 corners + center).',
  'Look for early signs: leaf damage, frass, eggs, or larvae.',
  'Record findings and report unusual pest outbreaks to extension officers.',
  'Use a notebook or mobile app to track pest levels over time.'
];

const LearnToScoutPage = () => (
  <div className="min-h-screen bg-gray-50 p-6">
    <h1 className="text-2xl font-bold mb-4 text-gray-800">How to Scout for Maize Pests</h1>
    <div className="bg-white rounded-lg shadow p-6 mb-8">
      <h2 className="text-lg font-semibold mb-2">Scouting Rules</h2>
      <ul className="list-disc pl-6 text-gray-700 space-y-1">
        {scoutingRules.map((rule, idx) => <li key={idx}>{rule}</li>)}
      </ul>
    </div>
    <div className="bg-white rounded-lg shadow p-6">
      <h2 className="text-lg font-semibold mb-4">Upcoming Scouting Events</h2>
      <ul className="divide-y divide-gray-200">
        {scoutingEvents.map((event, idx) => (
          <li key={idx} className="py-4">
            <div className="font-bold text-blue-700 text-base">{event.title}</div>
            <div className="text-gray-600 text-sm mb-1">{event.date} &mdash; {event.location}</div>
            <div className="text-gray-500 text-xs mb-1">Hosted by: {event.host}</div>
            <div className="text-gray-700 text-sm">{event.description}</div>
          </li>
        ))}
      </ul>
    </div>
  </div>
);

export default LearnToScoutPage;
